import * as Factory from "factory.ts";
import Faker from "faker";
import { jobTitlesRentalForDemo, nationalitiesRentalForDemo, religions } from "../config/FilterOptions";
import IWorkerRental from "../../../estreeh-common-library/build/interfaces/IWorkerRental";
import HourlySlotDefinitionFactory from "./HourlySlotDefinitionFactory";
import ReviewFactory from "./ReviewFactory";
import { demoWorkerSingleNames } from "../config/DemoItems";

const WorkerRentalFactory = Factory.Sync.makeFactory<IWorkerRental>({
  id: Factory.each((i) => i + 1),
  agency: null,
  jobTitle: Factory.each(() => Faker.random.arrayElement(jobTitlesRentalForDemo)),

  name: Factory.each(() =>       Faker.random.arrayElement(demoWorkerSingleNames) +
  " " +
  Faker.random.arrayElement(demoWorkerSingleNames)),
  nationality: Factory.each(() => ({
    title: Faker.random.arrayElement(nationalitiesRentalForDemo),
  })),
  phone: Factory.each(() => Faker.phone.phoneNumber()),
  rating: Factory.each(() => Faker.random.number({ min: 19, max: 50 })/10),
  reviews: Factory.each(() => ReviewFactory.buildList(Faker.random.number({min:3,max:280}))),
  religion: Factory.each(() => Faker.random.arrayElement(religions)),
  feesText: "",
  remarks: Factory.each(() =>
    Faker.random.arrayElement([undefined, undefined, "-----------"])
  ),
  smallPicture: Factory.each(() => ""),
  hourlySlotDefinitions: Factory.each(() =>
    HourlySlotDefinitionFactory.buildList(
      Faker.random.number({ min: 3, max: 3 })
    )
  ),
  hourlySlots: [],
  monthlySlots: [],
  hourlySlotsDictionary: {},
  hidden: false,
});

export default WorkerRentalFactory;
