import {
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  Grid,
  Typography,
} from "@material-ui/core";
import { RouteComponentProps, navigate } from "@reach/router";
import React, { Component } from "react";
import { IApplicationForm } from "../../../../estreeh-common-library/build/interfaces/IApplicationForm";
import IPaymentSummaryItem from "../../../../estreeh-common-library/build/interfaces/IPaymentSummaryItem";
import { IToolboxData } from "../../../../estreeh-common-library/build/interfaces/IToolboxData";
import { ToolboxSetter } from "../../../../estreeh-common-library/build/types/ToolboxSetter";
import NiceBox from "../common/NiceBox";
import ScrollToTop from "../common/ScrollToTop";
import RequestFormGeneral from "../request/RequestFormGeneral";
import RequestPaymentSummary from "../request/RequestPaymentSummary";
import Faker from "faker";
import RequestWorkerContainer from "../request/RequestWorkerContainer";
import { ApplicationFormFactory } from "../../factories/ApplicationFormFactory";
import RequestScheduleDisplay from "../request/RequestScheduleDisplay";
import TandCItem from "../common/TandCItem";

interface IRequestWorkerPageParams {
  isTwoYear: boolean;
  isRentalHourly: boolean;
  isRentalMonthly: boolean;
  link: string;
  isMd: boolean;
  toolboxData: IToolboxData;
  setToolbox: ToolboxSetter;
}

interface IRequestWorkerPage
  extends RouteComponentProps<IRequestWorkerPageParams> {}

class RequestWorkerPage extends Component<IRequestWorkerPage, any> {
  render() {
    const isRentalHourly =
      this.props.isRentalHourly !== undefined
        ? this.props.isRentalHourly
        : false;
    const isRentalMonthly =
      this.props.isRentalMonthly !== undefined
        ? this.props.isRentalMonthly
        : false;
    const link = this.props.link !== undefined ? this.props.link : "#";
    const isRental = isRentalHourly || isRentalMonthly;

    let workerTwoYear = this.props.isTwoYear
      ? this.props.toolboxData?.selectedWorkerTwoYear
      : undefined;
    let workerRental = isRental
      ? this.props.toolboxData?.selectedWorkerRental
      : undefined;

    let paymentItems: IPaymentSummaryItem[] = [
      {
        title: "Government Fees",
        amount: 50,
      },
      {
        title: "Processing Fees",
        amount: 1100,
      },
      {
        title: "Insurance Fees",
        amount: 200,
      },
      {
        title: "Accommodation Fees",
        amount: 70,
      },
      // {
      //   title: "Other Fees",
      //   amount:0,
      // },
      {
        title: "VAT (5%)",
        amount: 68.5,
      },
    ];

    const borderColor = "#0000001f";
    const border = "1px solid " + borderColor;
    const headerVariant = "h6";
    const showRentalFormFields = this.props.isTwoYear ? false : true;

    if (isRentalHourly) {
      paymentItems = [
        {
          title: "Service Charges (Incl. VAT)",
          amount: 0.4,
        },
        {
          title: "Worker Charges",
          amount: 8,
        },
      ];
    }
    if (isRentalMonthly) {
      paymentItems = [
        {
          title: "Service Charges (Incl. VAT)",
          amount: 3.1,
        },

        {
          title: "Worker Charges",
          amount: 200,
        },
        {
          title: "Delivery Charges",
          amount: 25,
        },
      ];
    }

    let fakeApp = ApplicationFormFactory.build();

    const selectedHours = isRentalHourly ? fakeApp.selectedHours : undefined;
    const selectedMonths = isRentalMonthly ? fakeApp.selectedMonths : undefined;

    const paymentRemarks =
      this.props.isTwoYear === true
        ? "Once the application has been sent, the agency will contact you within 1 business day."
        : "Currently we only accept payment as cash on delivery.";

    const onButtonClick = () => {
      let app: IApplicationForm = {
        id: 123,
        fullName: Faker.name.findName(),
        mobileNumber: "+9731193910",
        otherMobileNumber: "+9733300392",
        address: Faker.address.streetAddress(),
        workerRental: workerRental,
        workerTwoYear: workerTwoYear,
        specialNotes: "-",
        paymentSummaryItems: paymentItems,
        paymentRemarks: paymentRemarks,
        selectedHours: selectedHours,
        selectedMonths: selectedMonths,
        selectedHoursFrequency: fakeApp.selectedHoursFrequency,
      };
      if (this.props.setToolbox) {
        this.props.setToolbox((prevState) => ({
          ...prevState,
          applicationForm: app,
        }));
      }
      navigate(link);
    };
    return (
      <NiceBox p={3}>
        <ScrollToTop />
        <Grid container spacing={2} direction="row-reverse">
          <Grid item xs={12} md={7}>
            <Box>
              <Box pb={1}>
                <Typography variant={headerVariant}>
                  Selected Worker Details
                </Typography>
              </Box>
              <RequestWorkerContainer
                workerTwoYear={workerTwoYear}
                workerRental={workerRental}
              />
            </Box>
          </Grid>
          <Grid item xs={12} md={5}>
            <Box
              pr={this.props.isMd === true ? 2 : 0}
              borderRight={this.props.isMd === true ? border : "none"}
            >
              <RequestScheduleDisplay
                selectedHours={selectedHours}
                selectedMonths={selectedMonths}
                selectedHoursFrequency={fakeApp.selectedHoursFrequency}
              />
              <RequestFormGeneral
                showFrequency={showRentalFormFields}
                showAddress={showRentalFormFields}
                showEquipmentOption={false}
              />
              {isRentalHourly && (
                <Box py={1}>
                  <FormControlLabel
                    label="Bring Cleaning Tools (BHD 2 / Day)"
                    control={<Checkbox color="primary" />}
                  />
                </Box>
              )}
              {isRentalMonthly && (
                <Box py={1}>
                  <FormControlLabel
                    label="Bring Cleaning Tools (BHD 30 / Month)"
                    control={<Checkbox color="primary" />}
                  />
                  <FormControlLabel
                    label="Extra 2 hours per day (BHD 30 / Month)"
                    control={<Checkbox color="primary" />}
                  />
                  <FormControlLabel
                    label="Bring own Food (BHD 60 / Month)"
                    control={<Checkbox color="primary" />}
                  />
                </Box>
              )}
              <RequestPaymentSummary
                paymentItems={paymentItems}
                workerRental={workerRental}
                remarks={paymentRemarks}
              />
              <Box py={1}>
                <TandCItem title="Contract Terms & Conditions" />
                <TandCItem title="Local Transfer Terms & Conditions" />
                <TandCItem title="LMRA Terms & Conditions" />
                <TandCItem verb="I have read" title="Safety Guidelines" />
              </Box>
              <Box textAlign="center" pb={6} pt={2}>
                <Button
                  size="large"
                  variant="contained"
                  color="primary"
                  onClick={onButtonClick}
                >
                  Continue
                </Button>
              </Box>
            </Box>
          </Grid>
        </Grid>
      </NiceBox>
    );
  }
}

export default RequestWorkerPage;
